import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Logo from "./assets/light3.webp"
const MyComp = () => {
    return<Suspense fallback={
                        <Loader
        type="Puff"
        color="#00BFFF"
        height={100}
        width={100}
        timeout={3000} //3 secs
      />
                            }>
        <img src={Logo}
            alt="DIsplay" style={{
                display: "absolute",
                background: '#FFF',
                marginRight: "auto",
                marginTop: "30px",
                width: "100%",
                height: "auto",
                left: '350px',
                borderRadius: 20,
                filter: "drop-shadow(2.5px 2.5px 5px #000)",
            }}

            max-age="31536000"
        />
    </Suspense>;
};


export default MyComp;